import React,{Component} from 'react'
export default class ForumDetailHeadbar extends Component{
	render(){
		return (
			<div className='ForumDetailHeadbar'>
				<div className='back' onClick={e=>this.props.hideForumDetail(e)}> {'<'} </div>
				<div className='title'>{this.props.detailData.title}</div>
			</div>
		)
	}
}



