import React,{Component} from 'react'
import ForumNav from './ForumNav'
import ForumList from './ForumList'
import ForumFootBar from './ForumFootBar'

export default class Forum extends Component{
	constructor(props){
		super(props);
		this.state = {
			forumListData : [],
			forumType : 'good'
		}
	}
	getTopics(lis){
		fetch('//cnodejs.org/api/v1/topics?tab='+lis,{
			method : 'get'
		}).then((res)=>{
			res.json().then(data=>{
				this.setState({
					forumListData : data.data,
					forumType : lis
				})
			})
		})
	}
	componentDidMount(){
		this.getTopics(this.state.forumType)
	}
	render(){
		return (
			<div className='forum'>
				<ForumNav getTopics={e=>{this.getTopics(e)}}  />
				<div style={{height:"50px"}}></div>
				<ForumList forumlistData={this.state.forumListData} />
				<ForumFootBar />
			</div>
		)
	}
}



