import React ,{Component} from 'react'
export default class ForumNav extends Component{
	changeList(lis){
		this.props.getTopics(lis)
		document.documentElement.scrollTop = 0;
	}
	render(){
		let navList = ['all','ask','share','job','good'];
		return (
			<div className='nav'>
				<ul>
					{navList.map(lis=>{
						return <li key={lis} onClick={e=>{this.changeList(lis)}}>{lis}</li>
					})}
				</ul>
			</div>
		)
	}
}



