import React,{Component} from 'react'
import ForumDetail from './ForumDetail'
export default class ForumList extends Component{
	constructor(props){
		super(props)
		this.state={
			forumDetailData:[],
			forumDetailIsShow:false,
			forumListIsShow:true,
			winScrollH:0
		}
	}
	hideForumDetail(){
		this.setState({
			forumDetailData:[],
			forumDetailIsShow:false,
			forumListIsShow:true
		})
		document.documentElement.scrollTop = this.state.winScrollH;
		document.body.scrollTop = this.state.winScrollH;
	}
	getArticle(id){
		fetch('//cnodejs.org/api/v1/topic/'+id,{
			method :'get'
		}).then(res =>{
			res.json().then(data =>{
				this.setState({
					forumDetailData:data.data,
					forumDetailIsShow:true,
					forumListIsShow:false,
					winScrollH:document.documentElement.scrollTop || document.body.scrollTop
				})	
				document.documentElement.scrollTop = 0;
				document.body.scrollTop = 0;
			})
		})
	}
	render(){
		return (
			<div className='forumlist'>
				<ul style={{display:this.state.forumListIsShow ? "block" : "none"}}>
					{this.props.forumlistData.map(lis=>{
						return (
							<li key={lis.id} id={lis.id} onClick={e=>{this.getArticle(lis.id)}}>
								<div className='head'><img src={lis.author.avatar_url} alt={lis.author.loginname}/></div>
								<div className='info'>
									<h4>{lis.title}</h4>
									<p>{lis.reply_count}/{lis.visit_count}</p>
								</div>
								<div className='time'>{lis.create_at}</div>
							</li>
						)
					})}
				</ul>
				{
					this.state.forumDetailIsShow ?	<ForumDetail hideForumDetail={e=>this.hideForumDetail(e)}  forumDetailData={this.state.forumDetailData} /> :""
				}
			</div>
		)
	}
}



