import React,{Component} from 'react'
export default class ForumDetailContent extends Component{
	render(){
		return (
			<div className='content'>
				<div className='title'>{this.props.detailData.title}</div>
				<div className='info'>
					<span>发帖时间:{this.props.detailData.create_at}</span>
					<span>最后回复:{this.props.detailData.last_reply_at}</span>
					<span>浏览量:{this.props.detailData.visit_count}</span>
				</div>
				<div className='main' dangerouslySetInnerHTML={{__html:this.props.detailData.content}} />
			</div>
		)
	}
}



