import React,{Component} from 'react'
import ForumDetailHeadbar from './ForumDetailHeadbar'
import ForumDetailCommentList from './ForumDetailCommentList'
import ForumDetailContent from './ForumDetailContent'

export default class ForumDetail extends Component{
	constructor(props){
		super(props)
	}
	hideForumDetail(){
		this.props.hideForumDetail()
	}
	render(){
		return (
			<div className='forumDetail'>
				<ForumDetailHeadbar detailData={this.props.forumDetailData} hideForumDetail={e=>this.props.hideForumDetail(e)} />
				<div style={{height:"45px"}}></div>
				<ForumDetailContent detailData={this.props.forumDetailData} />
				<div className='title'>评论列表:</div>
				<ForumDetailCommentList  detailData={this.props.forumDetailData}/>			
			</div>
		)
	}
}



