import React,{Component} from 'react'
export default class ForumDetailComponentList extends Component{
	render(){
		return (
			<div className='commentList'>
				<ul>{
							this.props.detailData.replies.map(item=>{
								return (
									<li key={item.author.name}>
										<div>
											<div className='user'>
												<div className='head'><img src={item.author.avatar_url} alt={item.author.loginname}/></div>
												<div className='name'>{item.author.loginname}</div>
												<div className='time'>{item.create_at}</div>
											</div>
											<div className='cont' dangerouslySetInnerHTML={{__html:item.content}} />								
										</div>
									</li>
								)
							})
					
				}
				</ul>
				<div style={{height:"45px"}}></div>
			</div>
		)
	}
}



